* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100wh;
  height: 100vh;
  color: #fefefe;
  background-color: #424141;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.wrapper {
  width: 400px;
}

.caption {
  text-align: center;
  font-family: sans-serif;
}

.form {
  margin-top: 30px;
}

.form > :not(:last-child) {
  margin-bottom: 30px;
}

.email__text, .message__text {
  margin-bottom: 8px;
  font-family: sans-serif;
}

.email__field, .message__field {
  color: #424141;
  width: 100%;
  height: 30px;
  background-color: #fefefe;
  border: 1px solid #fff;
  border-radius: 8px;
  outline: 0;
  padding: 5px 8px;
  font-size: 14px;
  transition: all .2s;
}

.email__field:focus, .message__field:focus {
  outline: 3px solid #0d8eb6;
}

.email__field.confirm {
  outline: 3px solid #0db61b;
}

.email__field.wrong, .message__field.wrong {
  outline: 3px solid #ee4343;
}

.form__send-button {
  color: #fff;
  cursor: pointer;
  background: #327b92;
  border: 0;
  border-radius: 8px;
  margin: 0 auto;
  padding: 10px 48px;
  font-size: 18px;
  transition: all .2s;
  display: block;
}

.form__send-button:hover {
  background: #3ea7c9;
}

.form__send-button.disable {
  cursor: default;
  pointer-events: none;
  background: #4e525333;
}

